@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  min-height: 100vh;
  color: #fff;
  background: #ff0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container {
  width: 400px;
  height: 350px;
  background-color: #000;
  border-radius: 10px;
  place-items: center;
  padding: 0 0 10px;
  display: grid;
}

.container img {
  width: 200px;
  margin-bottom: -20px;
}

.container p {
  text-align: center;
  width: 80%;
}

.container .myLocationBtn {
  width: 180px;
  height: 50px;
  text-transform: capitalize;
  color: #fff;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  font-weight: 600;
}

.container .myLocationBtn:active {
  transform: scale(.95);
}

